import { prisma } from "@/lib/prisma";
import { ToolCard } from "@/components/tools/tool-card";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Search, Filter, Sparkles } from "lucide-react";
import Link from "next/link";

export const dynamic = 'force-dynamic';

interface PageProps {
  searchParams: Promise<{ category?: string; search?: string }>;
}

export default async function ToolsPage({ searchParams }: PageProps) {
  const params = await searchParams;
  const selectedCategory = params.category;
  const searchQuery = params.search;

  // Fetch all tools from database
  let tools: any[] = [];
  let categories: Array<{ value: string; label: string; count: number }> = [];
  let totalCount = 0;

  try {
    tools = await prisma.tool.findMany({
      where: {
        isActive: true,
        ...(selectedCategory && { category: selectedCategory }),
        ...(searchQuery && {
          OR: [
            { name: { contains: searchQuery, mode: 'insensitive' } },
            { description: { contains: searchQuery, mode: 'insensitive' } },
          ],
        }),
      },
      orderBy: {
        sortOrder: 'asc',
      },
    });

    totalCount = await prisma.tool.count({ where: { isActive: true } });

    // Get unique categories for filtering
    categories = [
      { 
        value: 'AI_WRITING', 
        label: 'AI Writing', 
        count: await prisma.tool.count({ where: { category: 'AI_WRITING', isActive: true } }) 
      },
      { 
        value: 'SEO_TOOLS', 
        label: 'SEO Tools', 
        count: await prisma.tool.count({ where: { category: 'SEO_TOOLS', isActive: true } }) 
      },
      { 
        value: 'DESIGN', 
        label: 'Design', 
        count: await prisma.tool.count({ where: { category: 'DESIGN', isActive: true } }) 
      },
      { 
        value: 'PRODUCTIVITY', 
        label: 'Productivity', 
        count: await prisma.tool.count({ where: { category: 'PRODUCTIVITY', isActive: true } }) 
      },
      { 
        value: 'CODE_DEV', 
        label: 'Code & Dev', 
        count: await prisma.tool.count({ where: { category: 'CODE_DEV', isActive: true } }) 
      },
      { 
        value: 'VIDEO_AUDIO', 
        label: 'Video & Audio', 
        count: await prisma.tool.count({ where: { category: 'VIDEO_AUDIO', isActive: true } }) 
      },
      { 
        value: 'OTHER', 
        label: 'Other', 
        count: await prisma.tool.count({ where: { category: 'OTHER', isActive: true } }) 
      },
    ].filter(cat => cat.count > 0);
  } catch (error: any) {
    console.error('Database error:', error?.message);
    tools = [];
    categories = [];
    totalCount = 0;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-purple-950 to-slate-900 pt-16">
      {/* Header */}
      <section className="relative overflow-hidden gradient-bg py-16 sm:py-20">
        <div className="absolute inset-0 bg-gradient-to-r from-purple-900/20 via-blue-900/20 to-slate-900/20 animate-gradient" />
        <div className="absolute inset-0 bg-grid-slate-400/[0.02] bg-[size:75px_75px]" />
        
        <div className="relative mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="text-center animate-fade-in-up">
            <div className="inline-flex items-center rounded-full px-4 py-1.5 bg-gradient-to-r from-purple-500/20 to-blue-500/20 border border-purple-500/30 text-purple-300 text-sm font-medium mb-6">
              <Sparkles className="w-4 h-4 mr-2" />
              Premium AI Tools Collection
            </div>
            <h1 className="text-4xl sm:text-5xl md:text-6xl font-bold mb-4 text-white">
              Browse Premium <span className="gradient-text">AI Tools</span>
            </h1>
            <p className="text-lg sm:text-xl text-gray-300 max-w-3xl mx-auto">
              Subscribe to individual tools or get multiple tools for maximum productivity.
              All tools come with instant access and monthly billing.
            </p>
          </div>
        </div>
      </section>

      {/* Main Content */}
      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8 py-12">
        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          {/* Sidebar - Filters */}
          <aside className="lg:col-span-1">
            <div className="glass rounded-xl p-6 sticky top-24 border border-white/10 animate-fade-in-up">
              {/* Search */}
              <div className="mb-6">
                <label className="block text-sm font-medium mb-2 text-gray-300">Search Tools</label>
                <form action="/tools" method="get" className="relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    type="search"
                    placeholder="Search tools..."
                    className="pl-10 bg-white/5 border-white/10 text-white placeholder:text-gray-500 focus:border-purple-500/50 focus:ring-purple-500/50"
                    defaultValue={searchQuery}
                    name="search"
                  />
                  {selectedCategory && (
                    <input type="hidden" name="category" value={selectedCategory} />
                  )}
                </form>
              </div>

              {/* Category Filter */}
              <div>
                <div className="flex items-center space-x-2 mb-3">
                  <Filter className="h-4 w-4 text-gray-400" />
                  <label className="block text-sm font-medium text-gray-300">Categories</label>
                </div>
                <div className="space-y-2">
                  <Link
                    href="/tools"
                    className={`flex items-center justify-between px-3 py-2 rounded-lg transition-all duration-300 ${
                      !selectedCategory
                        ? 'bg-gradient-to-r from-purple-600/20 to-blue-600/20 border border-purple-500/50 text-white'
                        : 'glass border border-white/10 text-gray-300 hover:bg-white/10 hover:text-white'
                    }`}
                  >
                    <span className="text-sm font-medium">All Tools</span>
                    <Badge variant="secondary" className="glass border-white/10 text-gray-300">
                      {totalCount}
                    </Badge>
                  </Link>

                  {categories.map((category, idx) => (
                    <Link
                      key={category.value}
                      href={`/tools?category=${category.value}`}
                      className={`flex items-center justify-between px-3 py-2 rounded-lg transition-all duration-300 animate-fade-in-up ${
                        selectedCategory === category.value
                          ? 'bg-gradient-to-r from-purple-600/20 to-blue-600/20 border border-purple-500/50 text-white'
                          : 'glass border border-white/10 text-gray-300 hover:bg-white/10 hover:text-white'
                      }`}
                      style={{ animationDelay: `${idx * 0.05}s` }}
                    >
                      <span className="text-sm font-medium">{category.label}</span>
                      <Badge variant="secondary" className="glass border-white/10 text-gray-300">
                        {category.count}
                      </Badge>
                    </Link>
                  ))}
                </div>
              </div>

              {/* Price Info */}
              <div className="mt-6 p-4 glass rounded-lg border border-purple-500/30 bg-gradient-to-br from-purple-500/10 to-blue-500/10">
                <h4 className="font-semibold text-sm mb-2 text-purple-300">Pricing Info</h4>
                <p className="text-xs text-gray-400">
                  All tools have monthly subscriptions. No hidden fees. Cancel anytime.
                </p>
              </div>
            </div>
          </aside>

          {/* Tools Grid */}
          <div className="lg:col-span-3">
            {/* Results Header */}
            <div className="mb-6 flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 animate-fade-in-up">
              <div>
                <h2 className="text-2xl sm:text-3xl font-bold text-white mb-1">
                  {selectedCategory
                    ? categories.find(c => c.value === selectedCategory)?.label || 'Tools'
                    : 'All Tools'}
                </h2>
                <p className="text-sm text-gray-400">
                  {tools.length} {tools.length === 1 ? 'tool' : 'tools'} available
                  {searchQuery && ` for "${searchQuery}"`}
                </p>
              </div>
              {searchQuery && (
                <Link
                  href="/tools"
                  className="text-sm text-purple-400 hover:text-purple-300 transition-colors"
                >
                  Clear search
                </Link>
              )}
            </div>

            {/* Tools Grid */}
            {tools.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
                {tools.map((tool, idx) => (
                  <div
                    key={tool.id}
                    className="animate-fade-in-up"
                    style={{ animationDelay: `${idx * 0.05}s` }}
                  >
                    <ToolCard tool={tool} />
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-20 glass rounded-xl border border-white/10 animate-fade-in-up">
                <div className="text-6xl mb-4">üîç</div>
                <h3 className="text-xl font-semibold mb-2 text-white">No tools found</h3>
                <p className="text-gray-400 mb-4">
                  Try adjusting your filters or search query
                </p>
                <Link
                  href="/tools"
                  className="inline-flex items-center px-4 py-2 rounded-lg bg-gradient-to-r from-purple-600 to-blue-600 hover:from-purple-500 hover:to-blue-500 text-white transition-all duration-300 hover:scale-105"
                >
                  Clear all filters
                </Link>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
